.projects {

	&-card {
		--_width: 100%;
		--_height: 100%;
		--_fs: .75rem;
		--_ls: normal;

		flex-direction: column;
		align-items: stretch;
		justify-content: stretch;
		gap: 0;
		text-align: start;
		text-transform: initial;

		@media (hover: hover) {

			&:hover {

				.projects-card-image {
					filter: saturate(100%);
				}
			}
		}

		&:is(:active, :focus-visible) {
			filter: saturate(100%);
		}

		&-image {
			width: 100%;
			height: 12.5rem;
			display: block;
			border-radius: 1.25rem;
			background-color: var(--gray-300);
			box-shadow: var(--shadow);
			object-fit: cover;
			filter: saturate(0%);
			transition-property: filter;
			transition-duration: var(--transition-duration);
			transition-timing-function: var(--transition-timing-function);

			@media (min-width: 992px) {
				height: 15.625rem;
			}
		}

		&-title {
			font-size: 1rem;
			font-weight: 300;
			letter-spacing: inherit;
			color: currentColor;
			margin-top: .5rem;
		}

		&-descr {
			min-height: 1.1675em;
			color: currentColor;
			margin-top: .125rem;
		}
	}
}