.modeling {

	&-card {
		--_width: 100%;
		--_height: 100%;
		--_rounded: 1.25rem;

		* {
			width: 100%;
			border-radius: inherit;
		}

		&-image {
			height: 15rem;
			display: block;
			object-fit: cover;
			box-shadow: var(--shadow);

			@media (min-width: 992px) {
				height: 18.25rem;
			}
		}
	}
}