@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(.5rem);
  }
  60% {
    transform: translateY(.25rem);
  }
}

@keyframes swipe-right {
  0%, 20% {
    transform: translateX(0);
  }
  60% {
    transform: translateX(-.5rem) rotate(-20deg);
  }
  100% {
    transform: translateX(0);
  }
}

.swiper-container {
	--swiper-theme-color: var(--gray-200);
	--swiper-navigation-sides-offset: -4rem;

	position: relative;

	.swiper {
		overflow: initial;

		&-slide {
			height: auto;

			> * {
				opacity: 0;
				transition-property: transform, opacity, filter, background, color, border;
				transition-duration: var(--transition-duration);
				transition-timing-function: var(--transition-timing-function);
			}

			&.swiper-slide-visible {
				> * {
					opacity: 1;
				}
			}
		}
	}
}

.swiper-button-prev,
.swiper-button-next {
	--_width: var(--swiper-navigation-size);
	--_height: var(--swiper-navigation-size);
	--_color: var(--swiper-navigation-color, var(--swiper-theme-color));
	--_bg: rgba(var(--gray-800-rgb), .5);
	--_rounded: 50%;
	--_padding: .25rem;

	--_color-hover: var(--swiper-navigation-color, var(--swiper-theme-color));
	--_color-active: var(--swiper-navigation-color, var(--swiper-theme-color));
	--_bg-hover: var(--gray-800);
	--_bg-active: var(--gray-800);

	--_icon-size: 100%;

	@media (max-width: 1840px) {
		display: none;
	}

	&::after {
		all: unset;
	}

	&.swiper-button-disabled {
		opacity: 0;
	}
}

.main-swiper {
	height: 100vh;
	height: 100svh;
	height: 100dvh;

	> * > .swiper-slide {
		overflow: hidden;
	}

	&-slide {
		height: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		padding-block: 3rem;

		> .container {
			height: 100%;
		}

		&-bg {
			width: 100%;
			height: 100%;
			display: block;
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			
			&-wrapper {
				width: 100%;
				box-shadow: .25rem .25rem 2rem 1rem rgba(0, 0, 0, 0.25);
				user-select: none;
				pointer-events: none;
				position: absolute;
				inset: 0;
				z-index: -1;

				@media (min-width: 992px) {
					width: 45%;
				}
			}
		}

		&-container {
			display: flex;
			flex-direction: column;
			justify-content: flex-end;
		}

		&-hint {
			display: flex;
			flex-direction: column;
			align-items: center;

			.icon-mouse,
			.icon-down {
				
				@media (hover: none) {
					display: none;
				}
			}

			.icon-mouse {
				--_size: 2.25rem
			}

			.icon-down {
				--_size: 1.25rem;

				@media (hover: hover) {
					animation: bounce 3s infinite ease-in-out;
				}
			}

			.icon-swipe {
				--_size: 3rem;

				@media (hover: hover) {
					display: none;
				}

				@media (hover: none) {
					animation: swipe-right 2s infinite ease-in-out;
				}
			}
		}
	}

	&-nav {
		
		.nav__list {
			> * {
				&:first-child {
					display: none;
				}
			}
		}
	}
}

.cards-swiper {
	--swiper-navigation-top-offset: calc(50% - 1rem);
}

.photos-swiper {

	.swiper-slide {
		> * {
			filter: saturate(0%);

			@media (hover: hover) {
				&:hover {
					filter: saturate(100%);
				}
			}

			&:is(:active, :focus-visible) {
				filter: saturate(100%);
			}
		}
	}
}

.swiper-filter {
	display: flex;
	gap: .375em;
}