.about {

	.section-header {
		@media (max-width: 992px) {
			margin-bottom: 1rem;
		}
	}

	&-info {
		display: flex;

		&-start {
			display: none;
			
			@media (min-width: 992px) {
				display: block;
				width: 45%;
			}
		}

		&-end {
			
			@media (min-width: 992px) {
				width: 50%;
				margin-left: auto;
			}
		}
	}
}