@keyframes bell-shake {
  0% {
    transform: rotate(0);
  }
  15% {
    transform: rotate(5deg);
  }
  30% {
    transform: rotate(-5deg);
  }
  45% {
    transform: rotate(4deg);
  }
  60% {
    transform: rotate(-4deg);
  }
  75% {
    transform: rotate(2deg);
  }
  100% {
    transform: rotate(0);
  }
}

.section {

	&-header {
		margin-bottom: 1.5rem;

		@media (min-width: 992px) {
			margin-bottom: 3rem;
		}
	}

	&-sub-button.btn {
		--_fw: 600;
		--_ls: normal;
		--_color: var(--gray-400);
		--_color-hover: var(--gray);
		--_color-active: var(--gray);

		--_icon-size: 1.5rem; 

		.icon {
			animation: bell-shake 0.6s cubic-bezier(0.36, 0.07, 0.19, 0.97) infinite;
		}
	}

	&-title,
	&-subtitle {
		color: var(--gray-400);
		text-transform: uppercase;
	}

	&-title {
		display: flex;
		align-items: center;
		gap: .25em;

		font-size: 2.75rem;
		font-weight: 600;
		letter-spacing: .25em;
		line-height: 1;

		@media (min-width: 992px) {
			font-size: 5rem;
		}

		@media (min-width: 1200px) {
			font-size: 6rem;
		}

		@media (min-width: 1400px) {
			font-size: 8.5rem;
		}

		&-start {
			
			@media (min-width: 992px) {
				width: 45%;
			}
		}

		&-end {
			margin-left: auto;

			@media (min-width: 992px) {
				width: 51.5%;
			}
		}
	}

	&-subtitle {
		font-size: 1.5rem;

		@media (min-width: 1400px) {
			font-size: 2.25rem;
		}

		@media (min-width: 1200px) {
			font-size: 1.75rem;
		}
	}
}