.events {

	&-card {
		--_width: 100%;
		--_height: 100%;

		--_ls: normal;
		--_bg: var(--gray-200);
		--_rounded: var(--rounded);
		--_padding: 2.25rem;

		--_bg-hover: var(--gray);
		--_bg-active: var(--gray);

		--_icon-size: 2.25rem;
		--_icon-color: var(--gray-200);
		
		min-height: 12.5rem;
		flex-direction: column;
		align-items: stretch;
		justify-content: stretch;
		gap: 0;
		text-align: start;
		text-transform: initial;
		box-shadow: var(--shadow);
		position: relative;

		@media (min-width: 992px) {
			--_padding: 2.5rem;

			min-height: 15.625rem;
		}

		&-title {
			font-size: 1.75rem;
			font-weight: inherit;
			line-height: 1.25;
			letter-spacing: inherit;
			text-transform: uppercase;
		}

		&-icon {
			background-color: var(--gray-400);
			border-radius: 50%;
			padding: .25rem;
			position: absolute;
			right: 1rem;
			bottom: 1rem;
		}
	}

	.swiper-filter {
		justify-content: flex-end;
		margin-top: 1.5rem;
	}
}